	-webkit-appearance: textfield;
	/* 1 */
	outline-offset: -2px;
	/* 2 */
}

/**
	* Remove the inner padding in Chrome and Safari on macOS.
	*/

[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

/**
	* 1. Correct the inability to style clickable types in iOS and Safari.
	* 2. Change font properties to `inherit` in Safari.
	*/

::-webkit-file-upload-button {
	-webkit-appearance: button;
	/* 1 */
	font: inherit;
	/* 2 */
}

/* Interactive
	  ========================================================================== */

/*
	* Add the correct display in Edge, IE 10+, and Firefox.
	*/

details {
	display: block;
}

/*
	* Add the correct display in all browsers.
	*/

summary {
	display: list-item;
}

/* Misc
	  ========================================================================== */

/**
	* Add the correct display in IE 10+.
	*/

template {
	display: none;
}

/**
	* Add the correct display in IE 10.
	*/

[hidden] {
	display: none;
}

li {
	list-style: none;
}

:root {
	font-size: 16px;
	-webkit-font-smoothing: antialiased;
}

body {
	margin: 0;
	color: var(--color-text);
	background-color: #000;
	font-family: $font-medium;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	--cursor-stroke: #000;
	--cursor-fill: none;
	--cursor-stroke-width: 1px;
	overflow-y: hidden;
}

::-webkit-scrollbar {
	display: none;
}

a {
	text-decoration: none;
	color: $white;
	outline: none;
}

a:hover,
a:focus {
	color: orange;
	outline: none;
}


.bg {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	z-index: -1;

	@media(max-width:576px) {
		min-height: calc(100vh - 69px);
		height: calc(100vh - 69px);
	}
}


.logo {
	position: absolute;
	top: 2rem;
	left: 2rem;

	@media (max-width: 576px) {
		width: 40px;
	}

	&--main {
		z-index: 3;
		top: 50%;
		width: 30%;
		left: 55%;
		-webkit-transform: translate(-50%, -50%);
		-ms-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
	}
}

#home {
	position: relative;
	z-index: 10000;
}

.bg-dots {
	-webkit-transform: translateY(-5%);
	-ms-transform: translateY(-5%);
	transform: translateY(-5%);

	#scene {
		img {
			width: 120%;
			height: 120%;
			top: -100% !important;
			left: -10% !important;

			@media (max-width: 1200px) {
				height: 100vh;
				width: auto;
			}
		}
	}
}


.element {
	position: absolute;

	&--right {
		top: 3.3rem;
		right: 3.2rem;
		z-index: 10000;
		padding: 1rem;

		@media (max-width: 991px) {
			right: 1.3rem;
			top: 1.3rem;
		}
	}

	&--dots {
		top: 0;
		left: 7%;
		width: 90%;
		height: 100%;
		z-index: 1;

		@media (max-width: 576px) {
			display: none;
		}
	}
}

div.main {
	height: 100vh;
	width: 100%;
}

section.main {
	min-height: 100vh;
	overflow: hidden;

	@media(max-width:576px) {
		min-height: calc(100vh - 69px);
		height: calc(100vh - 69px);
	}
}

.menu {
	grid-area: content;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	position: relative;
	-webkit-box-pack: start;
	-ms-flex-pack: start;
	justify-content: flex-start;
	-ms-flex-line-pack: center;
	align-content: center;
	max-width: 52vw;
	z-index: 1;
	height: auto;

	@media (max-width: 1700px) {
		max-width: 65vw;
	}

	@media (max-width: 1500px) {
		max-width: 70vw;
	}

	@media (max-width: 1400px) {
		max-width: 73vw;
	}

	@media (max-width: 1300px) {
		max-width: 78vw;
	}

	@media (max-width: 991px) {
		max-width: 85vw;
	}

	@media (max-width: 576px) {
		-webkit-transform: translateY(-10%);
		-ms-transform: translateY(-10%);
		transform: translateY(-10%);
		max-width: 81vw;
	}

}

.menu__item {
	font-size: 3.2rem;
	text-transform: uppercase;
	line-height: 1;
	cursor: pointer;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: start;
	-ms-flex-align: start;
	align-items: flex-start;
	position: relative;
	font-family: $font-medium;
	color: $grey;
	display: -ms-grid;
	display: grid;
	-ms-grid-columns: fit-content(400px) 1rem 1fr;
	grid-template-columns: fit-content(400px) 1fr;
	gap: 3rem;

	@media (max-width: 991px) {
		font-size: 1.75rem;
	}

	@media (max-width: 576px) {
		gap: 1rem;
	}
}

.menu__item:hover,
.menu__item:focus {
	color: $main;
}

.menu__item-inner-wrap {
	display: block;
	overflow: hidden;
	position: relative;
	height: 56px;

	@media (max-width: 576px) {
		height: 45px;
	}
}

.menu__item-inner {
	display: block;
	will-change: transform, opacity;
}

.menu__item-number {
	font-size: 1.5vw;
	font-family: soleil, sans-serif;
}

.hover-reveal {
	position: absolute;
	z-index: -1;
	width: 150px;
	height: 200px;
	top: 0;
	left: 0;
	pointer-events: none;
	opacity: 0;
	will-change: transform;
}

.hover-reveal__inner {
	overflow: hidden;
}

.hover-reveal__inner,
.hover-reveal__img {
	width: 100%;
	height: 100%;
	position: relative;
	will-change: transform;
}

.hover-reveal__img {
	background-size: cover;
	background-position: 50% 50%;
	-webkit-transform-origin: 50% 100%;
	-ms-transform-origin: 50% 100%;
	transform-origin: 50% 100%;
}

main {

	section {
		max-height: 100vh;
		max-width: 100%;
		height: 100vh;
	}
}

.cursor {
	display: none;
}

.content__title-inner,
.content__title-number,
.gallery__item-imginner,
.gallery__item-title,
.gallery__item-meta,
.gallery__item-more {
	will-change: transform, opacity;
}

@media screen and (min-width: 53em) {

	html,
	body {
		overflow: hidden;
	}

	main {
		grid-row-gap: 0;
	}

	.hover-reveal {
		width: 300px;
		height: 400px;
	}

	.gallery {
		grid-template-columns: repeat(auto-fill, minmax(175px, 1fr));
		grid-auto-columns: 175px;
		grid-auto-flow: column;
		-ms-grid-rows: auto;
		grid-template-rows: auto;
	}
}

@media (any-pointer: fine) {
	.cursor {
		position: fixed;
		top: 0;
		left: 0;
		display: block;
		pointer-events: none;
		z-index: 999999999999;
	}

	.cursor__inner {
		fill: $white;
		stroke: $white;
		stroke-width: 2px;
		opacity: 1;
	}

	.no-js .cursor {
		display: none;
	}
}

#page-wrap {
	position: absolute;
	top: 0;
	left: 0;
	max-height: 100vh;
	min-width: 100vw;

	@media (max-width: 576px) {
		display: none;
	}
}

/*** canvas ***/

#canvas,
#curtains-canvas {
	position: fixed;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100vw;
	z-index: 20;
	opacity: 0;
	-webkit-transition: opacity 0.5s ease-in;
	-o-transition: opacity 0.5s ease-in;
	transition: opacity 0.5s ease-in;
	pointer-events: none;
}

.planes-loaded #canvas {
	opacity: 1;
}

#content {
	position: relative;
	z-index: 15;
	margin: 0 auto;
	max-width: 30%;
}

#planes {
	overflow: hidden;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	-webkit-transition: opacity 0.5s ease-in;
	-o-transition: opacity 0.5s ease-in;
	transition: opacity 0.5s ease-in;
	height: 100vh;

	img {
		opacity: 0;
		display: none;
	}
}

.plane-wrapper {
	position: absolute;
	display: block;
	margin: 0 auto;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}

picture,
video {
	@media (max-width: 576px) {
		-webkit-transform: translateY(-1rem);
		-ms-transform: translateY(-1rem);
		transform: translateY(-1rem);
	}
}

#no-js {
	height: 100vh;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	background: #000;
    z-index: 1033;
    position: relative;
    color: $stroke;
	font-size: 1.5rem;
	text-align: center;
	text-transform: initial
}