.slide-leave,
.slide-enter {
  -webkit-transition: -webkit-transform 0.75s var(--easing);
  transition: -webkit-transform 0.75s var(--easing);
  -o-transition: transform 0.75s var(--easing);
  transition: transform 0.75s var(--easing);
  transition: transform 0.75s var(--easing), -webkit-transform 0.75s var(--easing);
}

.slide-leave .transition {
  -webkit-transform: translateY(100%);
      -ms-transform: translateY(100%);
          transform: translateY(100%);
}

.slide-leave-active .transition {
  -webkit-transition: -webkit-transform 0.75s var(--easing);
  transition: -webkit-transform 0.75s var(--easing);
  -o-transition: transform 0.75s var(--easing);
  transition: transform 0.75s var(--easing);
  transition: transform 0.75s var(--easing), -webkit-transform 0.75s var(--easing);
}

.slide-leave-to .transition {
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}
/* Cover down */
.slide-enter .transition {
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.slide-enter-active .transition {
  -webkit-transition: -webkit-transform 0.75s var(--easing);
  transition: -webkit-transform 0.75s var(--easing);
  -o-transition: transform 0.75s var(--easing);
  transition: transform 0.75s var(--easing);
  transition: transform 0.75s var(--easing), -webkit-transform 0.75s var(--easing);
}

.slide-enter-to .transition {
  -webkit-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
          transform: translateY(-100%);
}