.navigation {
    position: fixed;
    left: 2.35rem;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    z-index: 1032;

    .navbar {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        overflow: hidden;

        .nav-item {

            overflow: hidden;
            font-size: 3.65rem;
            @media (max-width: 1400px) {
                font-size: 3rem;
            }
            @media (max-width: 576px) {
                font-size: 2.75rem;
            }

            .nav-link {
                overflow: hidden;
                display: block;

                a {
                    overflow: hidden;
                    display: block;
                    text-transform: uppercase;
                    color: transparent;
                    -webkit-text-stroke: 1px $white;
                    font-family: $font-medium;
                    font-weight: 500;
                    @include transition();
                    padding-left: 1rem;

                    @media (max-width: 991px) {
                        -webkit-transition: none;
                        -o-transition: none;
                        transition: none;
                    }

                    &:hover {
                        color: $main;
                        -webkit-text-stroke: 1px transparent;
                    }
                }


            }

            &.active {
                .nav-link {
                    a {
                        color: $main;
                    }
                }
            }
        }
    }
}