@font-face {
    font-family: "oskari-medium";
    src: url('../fonts/oskari-g2-medium.otf');
    font-display: swap;
    font-style: normal;
    font-weight: 500;
}
@font-face {
    font-family: "oskari-regular";
    src: url('../fonts/oskari-g2-regular.otf');
    font-display: swap;
    font-style: normal;
    font-weight: 400;
}

@font-face {
    font-family: "physis-regular";
    src: url('../fonts/rnsphysis-medium.woff2');
    font-display: swap;
    font-style: normal;
    font-weight: 500;
}


$main: #00F292;
$white: #fff;
$stroke: #E6E7E8;
$grey: #D1D3D4;
$font-medium: "oskari-medium", sans-serif;
$font-regular: "oskari-regular", sans-serif;
$font-text: "physis-medium", sans-serif;
@mixin transition() {
    transition: 300ms cubic-bezier(.666,0,.666,.976);
}