.loader {
    position: relative;

    .overlay {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #000;
        height: 100vh;
        z-index: 10000;
    }

    .count {
        position: absolute;
        display: block;
        font-size: 8.5rem;
        font-family: $font-medium;
        color: transparent;
        -webkit-text-stroke: 1px $grey;
        left: 50%;
        top: 50%;
        -webkit-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        background-color: transparent;
        z-index: 999;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
            -ms-flex-direction: row;
                flex-direction: row;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        overflow: hidden;

        @media (max-width: 576px) {
            font-size: 4.5rem;
        }

        p {
            overflow: hidden;
            height: 15vw;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;

            @media (max-width: 992px) {
                height: 20vw;
            }

            span {
                display: block;
            }
        }
    }
}

#background {
    position: fixed;
    top: 0vh;
    left: 0;
    height: 125vh;
    opacity: 0.5;
    z-index: 0;
    width: 175vw;
    left: -37.5vw;

    @media (max-width: 991px) {
        height: 165vh;
        z-index: 0;
        width: 200vw;
        left: -50.5vw;
    }

    @media (max-width: 576px) {
        height: 165vh;
        z-index: 0;
        width: 200vw;
        left: -50.5vw;
    }
}